import React, {FC} from "react";
import { MapglStore } from "stores/MapglStore";
import {SearchStore} from "../stores/SearchStore";
import {
    AdditionalInfo, ArrowRightIcon,
    BottomInfo,
    BottomText, BuildingIcon,
    BuildingsIcon, Divider,
    GreenMarkerIcon,
    ShareIcon,
    Title
} from "../styles/cardStyles";

interface BuildingCardProps {
    isOpen: boolean;
    searchStore: SearchStore;
    routTime?: string;
    setCompaniesListIsOpen: (arg0: boolean) => void;
    shareLocation: (arg0: string) => void;
    mapglStore: MapglStore;
    coordinates: [number, number];
}

export const BuildingCard: FC<BuildingCardProps> = ({
                                                        isOpen,
                                                        searchStore,
                                                        routTime,
                                                        setCompaniesListIsOpen,
                                                        shareLocation,
                                                        mapglStore,
                                                        coordinates
                                                    }) => {
    return (
        <>
            {searchStore.currentObject?.address ? <BottomInfo>
                <GreenMarkerIcon onClick={() => mapglStore.setCenter(coordinates)}/>
                <BottomText onClick={() => mapglStore.setCenter(coordinates)}>
                    {searchStore.currentObject?.address?.postcode &&
                    searchStore.currentObject.address?.postcode + ", "}
                    {searchStore.currentObject?.address_name}
                </BottomText>
                {!isOpen ? <ShareIcon
                    onClick={() => shareLocation(searchStore.currentObject ? searchStore.currentObject?.address.building_id : "")}/> : null}
            </BottomInfo> : <BottomInfo>
                <GreenMarkerIcon/>
                <BottomText>
                    {searchStore.currentObject?.full_name}
                </BottomText>
                {!isOpen ? <ShareIcon
                    onClick={() => shareLocation(searchStore.currentObject ? searchStore.currentObject?.address.building_id : "")}/> : null}
            </BottomInfo>}
            {!!searchStore.currentObject?.links?.branches?.count ? (
                <BottomInfo>
                    <BuildingIcon/>
                    <BottomText>{searchStore.currentObject?.floors && searchStore.currentObject?.links?.branches?.count &&
                    `${searchStore.currentObject?.floors.ground_count} floor | ${searchStore.currentObject?.links?.branches?.count} companies` || searchStore.currentObject?.floors &&
                    `${searchStore.currentObject?.floors.ground_count} floor` ||
                    searchStore.currentObject?.links?.branches?.count && `${searchStore.currentObject?.links?.branches?.count} companies`
                    }</BottomText>
                </BottomInfo>
            ) : null}
            {searchStore.currentObject?.links?.branches?.count && isOpen ?
                (
                    <div>
                        <Divider style={{
                            height: "2px",
                        }}/>
                        <Title>Сompanies</Title>
                        <BottomInfo onClick={() => setCompaniesListIsOpen(true)} style={{
                            height: "72px",
                            width: "100%",
                            borderBottom: "solid 1px rgba(33, 33, 33, 0.08)",
                            alignItems: "center",
                            margin: "18px 0",
                        }}>
                            <BuildingsIcon/>
                            <div style={{
                                width: "360px",
                            }}>
                                <BottomText>All Companies</BottomText>
                                <AdditionalInfo>{`${searchStore.currentObject?.links?.branches?.count} companies`}</AdditionalInfo>
                            </div>
                            <ArrowRightIcon/>
                        </BottomInfo>
                    </div>
                ) : null
            }
        </>
    )
}
